























































 	var view
 	export default {
 		data() {
 			return {
 				status:'10',
 				curindex:1,
 				superviseList:[],
				searchvalue:"",
				overdue:'N'
 			};
 		},
		onShow() {
			if (uni.getStorageSync('zhandianisOverdue')) { //回退到此页面时判断是否需要刷新
			
			    var overdue =  uni.getStorageSync('zhandianisOverdue')				
				this.overdue = overdue				
				this.getList()			 
				uni.setStorageSync('zhandianisOverdue', false);
			}else{
				this.getList()
			}
		},
		watch:{
		   searchvalue:function(){		    
				this.getList()			   
		   }	
		},
 		created:function(){
 			view = this;
 		},
 		mounted:function(){
 			
 		},
 		methods:{
			shuaixuan:function(){
				uni.navigateTo({
				   url: '../shuaixuan/shuaixuan'
				})
			},
			gotodetail:function(item){					
				uni.setStorageSync('zhandianzhenggaiID',item.id);
				uni.navigateTo({
				   url: '../zhandianzhenggaiDetail/zhandianzhenggaiDetail'
				})
			},
 			stateqiehuan:function(index){
 				if(index == 1){
 					this.status = '10'
 				}else{
 					this.status = '20'
 				}
 				this.curindex = index
				this.getList()
 			},
 			getList:function(){
 				var msg ={
 					page:'1',
					pageSize:'10',
					enterpriseId:'',				
					siteId:'',
					address:this.searchvalue,
					status:this.status,
					rectifyEndDateBefore:'',
					rectifyEndDateAfter:'',
					patrolDateBefore:'',
					patrolDateAfter:'',
					overdue:this.overdue
 				}
 				this.$request({
 					url: 'findSitePatrolList',
 					method:'post',
					showLoading:true,
					loadingText:'加载中',
 					data:msg,
 					success:(jsonData) => {
 						console.log(777766)
 						console.log(jsonData)
 						if(jsonData.status){
 							  this.superviseList = jsonData.data
 						}else{
 							uni.showToast({
 								icon:'none',
 								title:jsonData.msg,
 								duration: 1000
 							});
 						}
 					},
 					complete() {
 						view.loading = false;
 					}
 				});
 			}
 		}
 	}
 